@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.reviews {
  padding-top: 84px; }
  .reviews h1 {
    font-size: 2em;
    margin-top: 0;
    text-align: center; }
  .reviews header {
    position: fixed;
    top: 0;
    width: 960px;
    max-width: calc(100% - 40px);
    padding: 20px 0px;
    background-color: #1c1f23; }
    .reviews header input {
      width: 100%;
      font-family: inherit;
      background-color: #353535;
      padding: 10px 20px;
      border: none;
      border-radius: 10px;
      font-size: 20px;
      color: #fff; }
  .reviews .show-more {
    text-align: center;
    padding: 20px;
    cursor: pointer;
    font-size: 20px; }
  .reviews ul.review-list li {
    background-color: #353535;
    padding: 10px;
    border-radius: 40px;
    margin-bottom: 10px; }
    .reviews ul.review-list li h2 {
      margin: 0; }
      @media all and (max-width: 960px) {
        .reviews ul.review-list li h2 {
          margin-top: 7px; } }
    .reviews ul.review-list li p {
      margin: 7px 0px; }
    .reviews ul.review-list li img {
      border-top-left-radius: 30px;
      border-bottom-left-radius: 30px; }
      @media all and (max-width: 960px) {
        .reviews ul.review-list li img {
          border-radius: 30px;
          display: block;
          margin: 0 auto; } }

html {
  overflow: hidden;
  height: 100%; }

body {
  font-family: Ubuntu,sans-serif;
  background-color: #1c1f23;
  color: #fff;
  margin: 0;
  overflow: auto;
  height: 100%; }

.content {
  max-width: 1000px;
  padding: 0px 20px;
  margin: 0 auto; }

a {
  color: inherit;
  text-decoration: inherit;
  display: inline-block; }

a:hover {
  text-decoration: underline; }

* {
  box-sizing: border-box; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

@media all and (min-width: 960px) {
  .row {
    display: flex;
    align-items: center; }
  .row .flex {
    flex: 1 1;
    margin-left: 20px; } }

